.menu {
  box-sizing: border-box;

  height: 100vh;
  background-color: #e5e5e5;
  font-weight: 600;
  color: $secondary-text-color;
  text-transform: uppercase;

  overflow: auto;

  @include onDesktop {
    font-weight: 700;
  }

  &__top {
    margin-bottom: 44px;

    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #e5e5e5;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    color: inherit;
    text-decoration: none;
    font-size: 16px;
    line-height: 27px;
  }

  &__test-ride {
    display: block;
    width: fit-content;

    padding-bottom: 7px;
    border-bottom: 1px solid $secondary-text-color;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  }
}
