.details {
  &__wrapper {
    display: grid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 48px;
    }
  }

  &__photos {
    @include pageGrid;
    margin-bottom: 16px;
  }

  &__link {
    position: relative;

    &--wide {
      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 1s;

    &:hover {
      @include hover (transform, scale(1.08));
    }
  }

  &__title {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
  }

  &__description {
    @include pageGrid;
    margin: 0;
  }

  &__description--position {
    grid-column: 1 / -1;

    @include onDesktop {
      grid-column: span 8;
    }
  }

  &__button {
    text-decoration: none;

    @include pageGrid;

    &__link {
      grid-column: span 2;
      height: 56px;
      line-height: 27px;
      font-weight: 700;

      border-radius: 40px;

      &:visited {
        box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.64);
      }

      &:hover {
        box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.4);
        cursor: pointer;
      }

      @include onTablet {
        grid-column: 2 / -2;
      }

      @include onDesktop {
        grid-column: 5 / -5;
      }
    }
  }
}
